import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
		token:""
    },
    mutations: {
        login(state, provider) {//
			state.token=provider,
			
            uni.setStorage({//
                key: 'token',
                data: provider
            })
        },
        logout(state) {//
			state.token="",
          
			uni.removeStorage({
			    key: 'token'
			})
			
        }
    }
})

export default store
